@charset "UTF-8";
.reset-page dl,
.reset-page dt,
.reset-page dd,
.reset-page ul,
.reset-page ol,
.reset-page li,
.reset-page pre,
.reset-page form,
.reset-page fieldset,
.reset-page input,
.reset-page p,
.reset-page blockquote,
.reset-page th,
.reset-page td {
  font-weight: 400;
  margin: 0;
  padding: 0; }

.reset-page h1,
.reset-page h2,
.reset-page h3,
.reset-page h4,
.reset-page h4,
.reset-page h5 {
  margin: 0;
  padding: 0; }

.reset-page table {
  border-collapse: collapse; }

.reset-page fieldset,
.reset-page img {
  border: 0 none; }

.reset-page fieldset {
  margin: 0;
  padding: 0; }

.reset-page fieldset p {
  margin: 0;
  padding: 0 0 0 8px; }

.reset-page legend {
  display: none; }

.reset-page address,
.reset-page caption,
.reset-page em,
.reset-page strong,
.reset-page th,
.reset-page i {
  font-style: normal;
  font-weight: 400; }

.reset-page table caption {
  margin-left: -1px; }

.reset-page hr {
  border-bottom: 1px solid #FFFFFF;
  border-top: 1px solid #E4E4E4;
  border-width: 1px 0;
  clear: both;
  height: 2px;
  margin: 5px 0;
  overflow: hidden; }

.reset-page ol,
.reset-page ul {
  list-style-image: none;
  list-style-position: outside;
  list-style-type: none; }

.reset-page caption,
.reset-page th {
  text-align: left; }

.reset-page q:before,
.reset-page q:after,
.reset-page blockquote:before,
.reset-page blockquote:after {
  content: ””; }
