@charset "UTF-8";
.base-btn {
  -webkit-transition: all .1s;
  transition: all .1s; }
  .base-btn:hover {
    opacity: 0.9;
    box-shadow: 0 2px 0 rgba(0, 0, 0, 0.045); }

/********************************** 主体颜色配置,不能只导出变量 *************************************************************/
.theme {
  color: #067785; }

html {
  font-size: 625%; }

body {
  font-size: 15px;
  margin: 0;
  color: #505050;
  font-family: 'Source Sans Pro', sans-serif, Lato, Helvetica Neue, Arial, PingFang SC, Hiragino Sans GB, STHeiti, Microsoft YaHei, WenQuanYi Micro Hei, sans-serif;
  -webkit-font-smoothing: antialiased;
  line-height: 1.5;
  word-wrap: break-word; }

* {
  box-sizing: border-box; }

blockquote {
  color: #666;
  margin: 0;
  padding-left: 3em;
  border-left: 0.5em #067785 solid; }

.ry-table table {
  max-width: 100%;
  white-space: inherit; }
  .ry-table table th,
  .ry-table table td {
    padding: 6px 13px;
    border: 1px solid #dfe2e5; }
